:root {
  --gridPadding: 20px;
}

body {
  background-color: rgb(20, 20, 20);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 100vh; */
  width: 100%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(30%, 1fr));
  grid-gap: 20px;
  width: 100%;
  height: calc(100% - (2 * var(--gridPadding)));
  padding: var(--gridPadding);
  background-color: rgb(20, 20, 20);
}

/* For portrait aspect ratios */
@media screen and (orientation: portrait) {
  .grid {
    grid-template-columns: repeat(auto-fill, minmax(45%, 1fr));
  }
}

.revealSliderItem {
  position: relative;
  width: 100%;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* overflow:hidden; */
  border-radius: 20px;
}

.revealSliderItem img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.flexboxContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: stretch;
  width: 100vw;
  height: auto;
}

.flexboxItem {
  flex: 0 0 auto;
  height: 200px;
  margin: 10px;
  width: auto;
}
